<template>
    <div>
        <!-- 头部 -->
         <div class="ed_topnav">
            <van-nav-bar
                title="修改地址"
                left-arrow
                @click-left="onClickLeft"
            />
        </div>

         <!-- 内容 -->
        <div class="ed_content">
            <van-address-edit
                :area-list="areaList"
                show-search-result
                :address-info="addressInfo"
                :search-result="searchResult"
                :area-columns-placeholder="['请选择', '请选择', '请选择']"
                @save="onSave"

            />
        </div>
    </div>
</template>

<script>
import areaList from "./erea.js"

    export default {
          data() {
            return {
                areaList,
                searchResult: [],
                addressInfo:{}
            };
        },
        methods:{
             onClickLeft() {
                this.$router.go(-1)
            },
            onSave({name,tel,province,city,county,areaCode,addressDetail}) {

                 let id = this.$route.query.id
                // 调用vuex方法
                this.$store.commit("address/edit",{
                    id,
                    name,
                    tel,
                    address:province+city+county+addressDetail,
                    province,
                    city,
                    county,
                    areaCode,
                    addressDetail
                });
                this.$router.go(-1); //回到上一页
            },
           
        },
         created(){
            //  获取路由的id
            let id = this.$route.query.id
            this.addressInfo = this.$store.state.address.lists.find(item=>item.id == id)

           
        },
    }
</script>

<style lang="scss" scoped>

</style>